.navbar{
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}

.navbar-logo{
    flex: 1;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    display: flex;
    align-items: center;
    height: 100%;
}
    
.navbar-logo-image-link{
  width: 100%;
}

.navbar-logo-image {
    object-fit: contain;
    height: 60%;
    width: 100%;
    cursor: pointer;
}

.navbar-menu-section{
    flex: 4;
}

.navbar-extras{
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  align-items: center;
  justify-content:start;
  margin-right: 2rem;
}

.nav-item {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.nav-links {
  font-weight: 400;
  color: black;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-item:hover {
  border-bottom: 2px solid greenyellow;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: gray;
  height: 100%;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
  /* height: 100%; */
}

.nav-item-submenu-container{  
  display: none;
  position: absolute;
  width: 100%;
  top: 100%;
}

.nav-item-submenu-content{
  display: flex;
  flex-direction: column;
}

.nav-item:hover .nav-item-submenu-container{
  display: unset;
}

.nav-item-submenu-container:hover{
  display: unset;
}

.nav-sub-link-container{
  background-color: black;
  width: 100%;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.nav-sub-link-container > a{
  color: #fff;
  width: 100%;
  font-size: 0.8rem;
  text-decoration: none;
}

.menu-toggle-icon{
  font-size: 0.7rem;
  margin-left: 0.7rem;
}

.nav-sub-link-container:hover .nav-sub-links{
  color: #E7B777;
  transition: all 0.2s ease-out;
}

@media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      justify-content: start;
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 70px;
      left: -200%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      justify-content: start;
      background: #f6fdfa;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      height: 100vh;
    }
  
    .nav-links {
      text-align: top;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      margin-left: 0rem;
    }
    
    .navbar-logo-image {
      width: 400%;
      object-fit: contain;
      justify-self: end;
    }
  
    .menu-icon {
      height: 100%;
      display: unset;
      font-size: 1rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: black;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #f6fdfa;
      color: #242424;
      transition: 250ms;
    }

    .navbar-extras {
      flex: 1;
      font-size: 1rem;
    }

    .navbar-extras > button{
      display: none;
    }
  }